/***
 * 
 * MAIN METRONOME COMPONENT
 * 
 */

class Metronome extends React.Component {
  constructor(props) {
    super(props);
    
    this.state = {
      tempo: 120,
      maxTempo: 240,
      minTempo: 30,
      beatsPerBar: 4,
      maxBeats: 12,
      minBeats: 1,
      clicksPerBeat: 1,
      maxClicks: 12,
      minClicks: 1,
      nextBeat: 0,
      nextBeatTime: 0,
      nextClick: 0,
      isPlaying: false,
      lookahead: 25,
      scheduleAheadTime: .1,
      nextNoteTime: 0,
      noteLength: .1,
      audioUnlocked: false,
      noteQueue: [],      
      isMenuOpen: false
    };
    
    this.handleChange = this.handleChange.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.handleTick = this.handleTick.bind(this);

    var AudioContext = window.AudioContext // Default
    || window.webkitAudioContext // Safari and old versions of Chrome
    || false; 

    this.audioContext = new AudioContext();


    this.timerWorker = worker();

    this.timerWorker.onmessage = (e) => {
        if (e.data === "tick") {
            this.handleTick();
        }
        else
            console.log("message: " + e.data);
    };


    this.timerWorker.postMessage({"interval":this.state.lookahead});
    
  }

  scheduleNote(note) {
    var osc = this.audioContext.createOscillator();
    var gainNode = this.audioContext.createGain();
    osc.connect(gainNode);
    gainNode.connect(this.audioContext.destination);
    if (note.beat !== 0) {
      osc.frequency.value = 440.0;
    } else {
      osc.frequency.value = 880.0;
    }
    if (note.click !== 0) {
      osc.frequency.value = 330.0;
    }

    osc.start( this.state.nextNoteTime);
    gainNode.gain.setTargetAtTime(0, this.state.nextNoteTime + this.state.noteLength, 0.015);
    osc.stop( this.state.nextNoteTime + this.state.noteLength*2);

  }

  handleTick() {
    while (this.state.nextNoteTime < this.audioContext.currentTime + this.state.scheduleAheadTime) {

      let nextBeat = this.state.nextBeat;
      let nextClick = this.state.nextClick;
    
      let nextNote = new Note(this.state.nextNoteTime, nextBeat, nextClick);

      let secondsPerBeat = 60.0 / this.state.tempo;


      let nextBT = this.state.nextBeatTime;

      if (nextNote.click === 0) {
        nextBT = nextNote.time + secondsPerBeat;

      }

      this.scheduleNote(nextNote);

      if (this.state.clicksPerBeat > 1) {
        nextClick++;
        if (nextClick >= this.state.clicksPerBeat) {
          nextClick = 0;
          nextBeat++;
          if (nextBeat >= this.state.beatsPerBar) {
            nextBeat = 0;
          }
        }
      } else {
        nextBeat++;
        nextClick = 0;
        if (nextBeat >= this.state.beatsPerBar) {
          nextBeat = 0;
        }
      }

      

      this.setState(state => {
        const queue = state.noteQueue.concat(nextNote);
        
        return {
          noteQueue: queue,
          nextNoteTime: state.nextNoteTime + 1/state.clicksPerBeat * secondsPerBeat,
          nextBeatTime: nextBT,
          nextBeat: nextBeat,
          nextClick: nextClick
        };
      });
    }
  }

  fadeAnimation(id) {
    document.getElementById(id).classList.remove("animation-fade");
    void document.getElementById(id).offsetWidth;
    document.getElementById(id).classList.add("animation-fade");
  }
  
  handleClick(e) {

    

    if (e.target.name === "increment-tempo") {
      this.fadeAnimation("tempo-label");

      let nextTempo = parseInt(this.state.tempo)+1;
      this.setState(state => ({tempo: nextTempo > state.maxTempo ? state.tempo : nextTempo}));
      return;
    } else if (e.target.name === "decrement-tempo") {
      this.fadeAnimation("tempo-label");

      let nextTempo = parseInt(this.state.tempo)-1;
      this.setState(state => ({tempo: nextTempo < state.minTempo ? state.tempo : nextTempo}));
      return;
    }  else if (e.target.name === "increment-beats") {
      this.fadeAnimation("beats");
      let nextBeats = parseInt(this.state.beatsPerBar)+1;
      this.setState(state => ({beatsPerBar: nextBeats > state.maxBeats ? state.beatsPerBar : nextBeats}));
      return;
    } else if (e.target.name === "decrement-beats") {
      this.fadeAnimation("beats");
      let nextBeats = parseInt(this.state.beatsPerBar)-1;
      this.setState(state => ({beatsPerBar: nextBeats < state.minBeats ? state.beatsPerBar : nextBeats}));
      return;
    } else if (e.target.name === "increment-clicks") {
      this.fadeAnimation("clicks");
      let nextClicks = parseInt(this.state.clicksPerBeat)+1;
      this.setState(state => ({clicksPerBeat: nextClicks > state.maxClicks ? state.clicksPerBeat : nextClicks}));
      return;
    } else if (e.target.name === "decrement-clicks") {
      this.fadeAnimation("clicks");
      let nextClicks = parseInt(this.state.clicksPerBeat)-1;
      this.setState(state => ({clicksPerBeat: nextClicks < state.minClicks ? state.clicksPerBeat : nextClicks}));
      return;
    }

    //
    /*var AudioContext = window.AudioContext // Default
    || window.webkitAudioContext // Safari and old versions of Chrome
    || false; */

    this.audioContext.close();

    this.audioContext = new AudioContext();
    //this.audioContext.resume();


    this.timerWorker.postMessage(this.state.isPlaying ? "stop" : "start");

    if (!this.state.audioUnlocked) {
      var buffer = this.audioContext.createBuffer(1, 1, 22050);
      var node = this.audioContext.createBufferSource();
      node.buffer = buffer;
      node.start(this.audioContext.currentTime);
    }

    this.setState(state => ({
      isPlaying: !state.isPlaying,
      audioUnlocked: true,
      noteQueue: [],
      nextNoteTime: 0,
      nextBeat: 0,
      nextClick: 0
    }));
  }
  
  handleChange(e) {
    if (e.target.name === "beats") {
      this.setState({beatsPerBar: e.target.value});
    } else if (e.target.name==="clicks") {
      this.setState({clicksPerBeat: e.target.value});
    } else {
      this.fadeAnimation("tempo-label");

      console.log(e.target.value);
      this.setState({tempo: e.target.value});
    }

  }

  
  
  render() {
    

    return (
      <div className="metronome">
        <Animation tempo={this.state.tempo} color={this.props.color} nbt={this.state.nextBeatTime} curBeat={this.state.nextBeat} bpb={this.state.beatsPerBar} isplaying={this.state.isPlaying} queue={this.state.noteQueue} audiocontext={this.audioContext} />
        <div className="inputs">
        <Button 
            value={this.state.isPlaying ? "Stop" : "Start"}
            onClick={this.handleClick} />
          <TempoInputs onChange={this.handleChange} tempo={this.state.tempo} onClick={this.handleClick} maxTempo={this.state.maxTempo} minTempo={this.state.minTempo} />
          
          <div className="input-row">
            <div className="spinner-inputs">
              <Spinner label="Beats per bar" id="beats" onClick={this.handleClick} value={this.state.beatsPerBar} />
              <Spinner label="Clicks per beat" id="clicks" onClick={this.handleClick} value={this.state.clicksPerBeat} />
            </div>
          </div>
        </div>
      </div>
    );
  }
}





